.site {
  display: grid;
  grid-template-columns: 1fr minmax(0, 1000px) 1fr;
  grid-template-areas:
    "header header header"
    ". main ."
    ". sidebar ."
    "footer footer footer";
}

.site-header {
  grid-area: header;
}

.site-main {
  grid-area: main;
  overflow: hidden; /* Resolves issue with <pre> elements forcing full width. */
  // min-height: 100vh;
}

.widget-area {
  grid-area: sidebar;
}

.site-footer {
  grid-area: footer;
}

@media (min-width: 50em) {
  .widget-area {
    display: flex;
    justify-content: space-between;
    align-items: center;
    // gap: 2rem;
  }
}

@import "no-sidebar";
